"use client";

import { useRouter } from "next/navigation";

interface NavbarProps {
  user: {
    username: string;
    role: string;
    dept: string;
  } | null;
}

export default function Navbar({ user }: NavbarProps) {
  const router = useRouter();

  const handleLogout = () => {
    localStorage.removeItem("user");
    router.push("/login");
  };

  return (
    <nav className="bg-green-600 text-white shadow-md border-b border-gray-200 px-6 py-4 flex justify-between items-center relative">
      {/* Logo / Title */}
      <div className="flex text-white items-center">
        <h1
          className="text-xl font-bold tracking-tight cursor-pointer"
          onClick={() => router.push("/dashboard")}
        >
          Ticketing System
        </h1>
      </div>

      {/* Navigation Links */}
      {user && (
        <div className="absolute left-1/2 transform -translate-x-1/2 flex items-center space-x-4">
          <button
            onClick={() => router.push("/dashboard")}
            className="text-white hover:text-gray-100 px-3 py-2 rounded-md text-sm font-large transition-colors"
          >
            Dashboard
          </button>
          <button
            onClick={() => router.push("/tickets")}
            className="text-white hover:text-gray-100 px-3 py-2 rounded-md text-sm font-large transition-colors"
          >
            Tickets
          </button>
        </div>
      )}

      {/* User Info & Logout */}
      {user && (
        <div className="flex items-center space-x-6">
          <div className="text-right hidden sm:block">
            <p className="text-sm font-medium text-white">{user.username}</p>
            <p className="text-xs text-gray-400">{user.dept}</p>
          </div>

          <button
            onClick={handleLogout}
            className="w-[100px] h-[40px] flex items-center justify-center text-sm font-medium text-red-600 bg-red-50 hover:bg-red-100 border border-red-200 rounded-md transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500"
          >
            Logout
          </button>
        </div>
      )}
    </nav>
  );
}
